import Button from "../../components/Button/Button";
import Container from "../../components/Container/Container";
import Paragraph from "../../components/Paragraph/Paragraph";
import { NavHashLink } from "react-router-hash-link";

import {
  Wrapper,
  ContainerWrapper,
  BackgroundImage,
  StyledHeading2,
  ButtonsWrapper,
} from "./style";
import image from "../../assets/images/CallToActionBGC.jpg";

const CallToAction = () => {
  return (
    <Wrapper>
      <BackgroundImage src={image} loading="lazy" alt="Betonowe to" />
      <Container>
        <ContainerWrapper>
          <StyledHeading2>Planujesz inwestycj?</StyledHeading2>
          <Paragraph light="true">
            Skontaktuj si z nami i przekonaj si, jak moemy pomc zrealizowa
            Twoj wizj.
          </Paragraph>
          <ButtonsWrapper>
            <NavHashLink to="/projartweb#kontakt">
              <Button transparent>Zadzwo teraz</Button>
            </NavHashLink>
          </ButtonsWrapper>
        </ContainerWrapper>
      </Container>
    </Wrapper>
  );
};

export default CallToAction;
