import Button from "../../components/Button/Button";
import Container from "../../components/Container/Container";
import Paragraph from "../../components/Paragraph/Paragraph";
import { NavHashLink } from "react-router-hash-link";

import {
  Wrapper,
  ContainerWrapper,
  BackgroundImage,
  StyledHeading1,
  ButtonsWrapper,
} from "./style";

import image from "../../assets/images/HeroImage.jpg";

const Header = () => {
  return (
    <Wrapper>
      <BackgroundImage
        src={image}
        loading="lazy"
        alt="Konstrukca, budynek, to nagwka"
      />
      <Container>
        <ContainerWrapper>
          <StyledHeading1>Z pasji do nowoczesnego budownictwa.</StyledHeading1>
          <Paragraph light="true">
            Tworzymy nowoczesne przestrzenie z dbaoci o kady detal.
            Dowiadczenie, innowacja i pasja  to fundamenty naszych realizacji.
          </Paragraph>
          <ButtonsWrapper>
            <NavHashLink to="/projartweb#oferta">
              <Button white>Poznaj ofert</Button>
            </NavHashLink>
            <NavHashLink to="/projartweb#onas">
              <Button transparent>O firmie</Button>
            </NavHashLink>
          </ButtonsWrapper>
        </ContainerWrapper>
      </Container>
      {/* <ShapeDividerBottom /> */}
    </Wrapper>
  );
};

export default Header;
