import ArticleAndPhoto from "../../components/ArticleAndPhoto/ArticleAndPhoto";
import TextWithChildrenComponent from "../../components/TextWithChildrenComponent/TextWithChildrenComponent";
import Paragraph from "../../components/Paragraph/Paragraph";
import Container from "../../components/Container/Container";

import imageWindow from "../../assets/images/Zespol2.jpg";
import pmImage from "../../assets/images/pm.jpg";
import glassImage from "../../assets/images/Balustrady.jpg";
import swiadectewo from "../../assets/images/sche.jpg";
import UnorderedList from "../../components/UnorderedList/UnorderedList";

const Services = () => {
  return (
    <Container>
      <TextWithChildrenComponent
        id="oferta"
        title="Oferta"
        text="Dziki kompleksowemu podejciu zapewniamy sprawny przebieg realizacji projektw oraz estetyczne i funkcjonalne rozwizania architektoniczne. Nasze dowiadczenie oraz dbao o detale gwarantuj najwysz jako na kadym etapie wsppracy."
      >
        <ArticleAndPhoto image={pmImage} altText="Project Manager">
          <h3>
            Prowadzenie inwestycji budowlanych w ramach funkcji Inwestora
            zastpczego lub Project Managera
          </h3>
          <Paragraph>
            Penimy rol reprezentanta Inwestora na kadym etapie realizacji
            projektu, zapewniajc kontrol nad przebiegiem inwestycji,
            terminowo oraz najwysz jako wykonania.
          </Paragraph>
          <Paragraph>Kluczowe zadania:</Paragraph>
          <UnorderedList>
            <li>
              <Paragraph>
                Reprezentacja Inwestora podczas wyboru uczestnikw procesu
                budowlanego
              </Paragraph>
            </li>
            <li>
              <Paragraph>
                Kontrola zgodnoci realizacji inwestycji i jakoci prac
                budowlanych
              </Paragraph>
            </li>
            <li>
              <Paragraph>
                Nadzr inwestorski nad wszystkimi branami, zgodnie z przepisami
                ustawy Prawo budowlane i innymi obowizujcymi przepisami
              </Paragraph>
            </li>
            <li>
              <Paragraph>
                Potwierdzanie faktycznie wykonanych robt oraz kontrola
                rozlicze budowy
              </Paragraph>
            </li>
            <li>
              <Paragraph>
                Nadzr nad harmonogramem realizacji inwestycji
              </Paragraph>
            </li>
            <li>
              <Paragraph>
                Akceptacja podwykonawcw i zaproponowanych przez nich
                materiaw, weryfikacja rozlicze z GW w ramach solidarnej
                odpowiedzialnoci
              </Paragraph>
            </li>
            <li>
              <Paragraph>
                Weryfikacja zaawansowania robt i nadzr nad dokumentacj
                powykonawcz
              </Paragraph>
            </li>
            <li>
              <Paragraph>
                Wsparcie w procedurze uzyskania odbioru kocowego i Pozwolenia
                na Uytkowanie (PnU)
              </Paragraph>
            </li>
          </UnorderedList>
        </ArticleAndPhoto>
        <ArticleAndPhoto
          image={glassImage}
          textfirst="true"
          altText="Balustrady szklane"
        >
          <h3>Projektowanie i wykonawstwo konstrukcji szklanych</h3>
          <Paragraph>
            Tworzymy nowoczesne i eleganckie rozwizania szklane, ktre nadaj
            przestrzeniom lekkoci i prestiu. Nasze konstrukcje s nie tylko
            estetyczne, ale rwnie funkcjonalne i bezpieczne.
          </Paragraph>
          <Paragraph>Oferujemy:</Paragraph>
          <UnorderedList>
            <li>
              <Paragraph>
                Szklane zadaszenia wej  nowoczesna ochrona przed warunkami
                atmosferycznymi.
              </Paragraph>
            </li>
            <li>
              <Paragraph>
                Szklane (bezprofilowe) cianki dziaowe - eleganckie podziay
                przestrzeni wewntrznej.
              </Paragraph>
            </li>
            <li>
              <Paragraph>
                Szklane drzwi wewntrzne  przesuwne lub otwierane, idealne do
                gabinetw i azienek.
              </Paragraph>
            </li>
            <li>
              <Paragraph>
                Szklane balustrady  dodajce lekkoci antresolom i schodom.
              </Paragraph>
            </li>
          </UnorderedList>
          <Paragraph>
            Wszystkie rozwizania projektowane s indywidualnie pod kad
            realizacj.
          </Paragraph>
          <Paragraph>
            W ramach wykonawstwa i montau dostarczamy kompletn
            dokumentacj/projekt instalowanych produktw.
          </Paragraph>
        </ArticleAndPhoto>
        <ArticleAndPhoto
          image={imageWindow}
          altText="Zdjcie przedstawiajce zesp firmy"
        >
          <h3>
            Doradztwo oraz niezalene opinie techniczne w zakresie stolarki
            okiennej i drzwiowej
          </h3>
          <Paragraph>
            Stolarka budowlana spenia obecnie bardzo istotn rol w
            nowoczesnych budynkach i z tego powodu poddawana jest szeregom
            wymaga zawartych w specyfikacjach technicznych.
          </Paragraph>
          <Paragraph>
            W ramach naszych usug oferujemy nastpujce usugi, zwizane z
            okreleniem waciwoci stolarki:
          </Paragraph>
          <UnorderedList>
            <li>
              <Paragraph>
                analiza i weryfikacja oblicze cieplnych stolarki  wsp.
                przenikania ciepa Uw, Ud.
              </Paragraph>
            </li>
            <li>
              <Paragraph>
                obliczenia statyczne konstrukcji i odporno na obcienie
                wiatrem (klasyfikacja),
              </Paragraph>
            </li>
            <li>
              <Paragraph>
                dobr rozwiza zwizanych z izolacja akustyczn,
              </Paragraph>
            </li>
            <li>
              <Paragraph>
                dobr rozwiza zwizanych z bezpieczestwem uytkowania
                konstrukcji,
              </Paragraph>
            </li>
            <li>
              <Paragraph>
                indywidualna analiza projektu budowlanego i dobr rozwiza
                zapewniajcych wymagan funkcjonalno,
              </Paragraph>
            </li>
            <li>
              <Paragraph>
                opinie techniczne i ekspertyzy zamontowanej stolarki,
              </Paragraph>
            </li>
            <li>
              <Paragraph>
                mediacje przedsdowe, w celu rozwizania sporw pomidzy
                inwestorem (zamawiajcym) a wykonawc / producentem / firm
                montaow stolarki.
              </Paragraph>
            </li>
          </UnorderedList>
        </ArticleAndPhoto>
        <ArticleAndPhoto
          image={swiadectewo}
          textfirst="true"
          altText="Przykadowe wiadectwo charakterystyki energetycznej budynku"
        >
          <h3>wiadectwa charakterystyki energetycznej budynkw</h3>
          <Paragraph>
            wiadectwo charakterystyki energetycznej budynkw to dokument, ktry
            okrela zapotrzebowanie budynku na nieodnawialn energi pierwotn
            Ep i zgodnie z nowelizacj ustawy o charakterystyce energetycznej
            budynkw oraz zmianach w ustawie prawo budowlane (Dz.U.2023.682) od
            dnia 28 kwietnia 2023r. jest obligatoryjnym dokumentem gdy:
          </Paragraph>
          <UnorderedList>
            <li>
              <Paragraph>
                zakoczona zostaje budowa i skadane jest do urzdu
                zawiadomienie o zakoczeniu budowy obiektu budowlanego lub
                wniosek o udzielenie pozwolenia na uytkowanie,
              </Paragraph>
            </li>
            <li>
              <Paragraph>budynek, cz budynku lub lokal bdzie:</Paragraph>
              <UnorderedList>
                <li>
                  <Paragraph>zbywany na podstawie umowy sprzeday,</Paragraph>
                </li>
                <li>
                  <Paragraph>
                    zbywany na podstawie umowy sprzeday spdzielczego
                    wasnociowego prawa do lokalu,
                  </Paragraph>
                </li>
                <li>
                  <Paragraph>wynajty.</Paragraph>
                </li>
              </UnorderedList>
            </li>
          </UnorderedList>
          <Paragraph>
            Szczegowe informacje na temat wiadectw charakterystyki
            energetycznej budynkw dostpne s na stronie Ministerstwa Rozwoju i
            Technologii.
          </Paragraph>

          <Paragraph>
            Nasza oferta obejmuje kompleksowe opracowanie wiadectw oraz audytu
            energetycznego dla wszystkich rodzajw budynkw.
          </Paragraph>
          <Paragraph>
            Obliczenia s wykonywane indywidualnie w oparciu o przekazane dane
            projektowe, indywidualny wywiad, wizj lokaln i inwentaryzacj
            obiektu.
          </Paragraph>
        </ArticleAndPhoto>
      </TextWithChildrenComponent>
    </Container>
  );
};

export default Services;
