import { Wrapper, ButtonsWrapper } from "./style";
import ArticleAndPhoto from "../../components/ArticleAndPhoto/ArticleAndPhoto";
import TextWithChildrenComponent from "../../components/TextWithChildrenComponent/TextWithChildrenComponent";
import Paragraph from "../../components/Paragraph/Paragraph";
import Container from "../../components/Container/Container";
import UnorderedList from "../../components/UnorderedList/UnorderedList";
import Button from "../../components/Button/Button";

import image from "../../assets/images/Zespol1.jpg";

const AboutUs = () => {
  return (
    <Container>
      <TextWithChildrenComponent title="O nas" text="" id="onas">
        <ArticleAndPhoto
          textfirst
          image={image}
          altText="Zdjcie przedstawiajce zesp firmy"
        >
          <Wrapper>
            <Paragraph>
              Nasza firma powstaa w 2004 roku z pasji do tworzenia nowoczesnych
              rozwiza budowlanych. Zaoyciel firmy, Krzysztof Smolnik 
              inynier i absolwent Politechniki Gdaskiej  od ponad 20 lat
              realizuje ambitne projekty, dostarczajc innowacyjne rozwizania w
              brany budowlanej.
            </Paragraph>
            <Paragraph>Specjalizujemy si w:</Paragraph>
            <UnorderedList>
              <li>
                <Paragraph>
                  Prowadzeniu inwestycji budowlanych jako Project Manager 
                  kompleksowe wsparcie na kadym etapie realizacji.
                </Paragraph>
              </li>
              <li>
                <Paragraph>
                  Obsudze i serwisie budynkw, zapewniajc ich pen
                  funkcjonalno.
                </Paragraph>
              </li>
              <li>
                <Paragraph>
                  Projektowaniu i wykonawstwie konstrukcji szklanych, ktre
                  nadaj przestrzeni nowoczesny charakter.
                </Paragraph>
              </li>
            </UnorderedList>

            <Paragraph>
              Zachcamy do pobrania wanych dokumentw zwizanych z naszymi
              usugami. Kliknij poniej, aby uzyska dostp do referencji oraz
              potwierdzenia wpisu na wykaz osb upowanionych do sporzdzania
              wiadectw charakterystyki energetycznej.
            </Paragraph>

            <ButtonsWrapper>
              <Button
                downloadButton
                downloadFile="List referencyjny Saferoad - K.Smolnik.pdf"
              >
                Pobierz referencje
              </Button>
              <Button
                downloadButton
                downloadFile="Potwierdzenie wpisu CHE 27.02.2025.pdf"
              >
                Pobierz zawiadczenie
              </Button>
            </ButtonsWrapper>
          </Wrapper>
        </ArticleAndPhoto>
      </TextWithChildrenComponent>
    </Container>
  );
};

export default AboutUs;
